// ------------------------------------------ //
// MIXINS
// ------------------------------------------ //
@mixin wrapper {
position: relative;

display: flex;
flex-direction: column;

align-items: center;
align-content: center;
justify-content: center;

    &__wrapper {
position: relative;

width: 100%;
margin: 0 auto;
padding: 6rem 2.5rem;

        @media (min-width: $large-up) {
width: $screen-large-min;
}
}
}

@mixin headings {
text-align: center;

    &__heading {
width: 100%;
padding: 1rem 0;

text-align: center;

color: $color-primary-text-dark;

font-size: 2.5rem;
line-height: 1.2;

        @media (min-width: $large-up) {
font-size: 5rem;
}
}

    &__subheading {
font-size: $font-size--1;
font-weight: $bold-font-weight;

        @media (min-width: $large-up) {
font-size: $font-size-01;
}
}
}